import React from "react"
import { Row, Col, Card, CardBody, Input, FormGroup, Button  } from "reactstrap"
import Breadcrumbs from "../../components/@vuexy/breadCrumbs/BreadCrumb"
import Radio from "../../components/@vuexy/radio/RadioVuexy"
import {connect} from "react-redux"

class CompanySetting extends React.Component {
  state = {
    dob: new Date("1995-05-22")
  }

  handledob = date => {
    this.setState({
      dob: date
    })
  }
  saveChanges () {

  }
  render() {
      return (
        <React.Fragment>
          <div>
            <Breadcrumbs
              breadCrumbTitle="Company Setting"
              breadCrumbParent="Admin"
              breadCrumbActive="Company Setting"
            />
            <Card>
                <CardBody>
                  <div className="mb-2">
                    <h5 className="mb-1">
                        {/* <User className="mr-50" size={16} /> */}
                        <span className="align-middle">Pack is included in ACV</span>
                    </h5>
                    <hr></hr>
                    <Row>
                      <Col sm="3" xs="6" className="text-right">
                        <div className="p" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >Yes</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <Radio
                          color="success"
                          defaultChecked={true}
                          name="pick_acv"
                        />
                      </Col>
                      <Col sm="3" xs="6" className="text-right">
                        <div className="p" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >No</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <Radio
                          color="info"
                          defaultChecked={false}
                          name="pick_acv"
                        />
                      </Col>
                    </Row>
                  </div>
                  <div className="mb-2">
                    <h5 className="mb-1">
                        {/* <User className="mr-50" size={16} /> */}
                        <span className="align-middle">Default Deal Pack</span>
                    </h5>
                    <hr></hr>
                    <Row>
                      <Col sm="3" xs="6">
                        <div className="p text-right" style={{paddingTop: "5px"}}>
                            <span className="mr-1" >Default Deal Pack</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <FormGroup>
                          <Input type="text" bsSize="md" placeholder="Large Input" />
                        </FormGroup>
                      </Col>
                    </Row>
                  </div>
                  <div className="mb-2">
                    <h5 className="mb-1">
                      {/* <User className="mr-50" size={16} /> */}
                      <span className="align-middle">Deal Doc Fee</span>
                    </h5>
                    <hr></hr>
                    <Row>
                      <Col sm="3" xs="6">
                        <div className="p text-right" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >Track Doc Fee</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6"> 
                        <Radio
                          color="success"
                          defaultChecked={true}
                          name="deal_doc"
                        />
                      </Col>
                      <Col sm="3" xs="6">
                        <div className="p text-right" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >Do Not Track</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6"> 
                        <Radio
                          color="info"
                          defaultChecked={false}
                          name="deal_doc"
                        />
                      </Col>
                      <Col sm="3" xs="6">
                        <div className="text-right pt-2">
                            <span className="mr-1" >Default Doc Fee</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6" className="pt-1">
                        <FormGroup>
                          <Input type="text" bsSize="md" placeholder="Large Input" />
                        </FormGroup>
                      </Col>
                    </Row>
                  </div>
                  <div className="mb-2">
                    <h5 className="mb-1">
                        {/* <User className="mr-50" size={16} /> */}
                        <span className="align-middle">Show Flooring Section</span>
                    </h5>
                    <hr></hr>
                    <Row>
                      <Col sm="3" xs="6" className="text-right">
                        <div className="p" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >Show Flooring</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <Radio
                          color="success"
                          defaultChecked={true}
                          name="flooring"
                        />
                      </Col>
                      <Col sm="3" xs="6" className="text-right">
                        <div className="p" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >Hide Flooring</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <Radio
                          color="info"
                          defaultChecked={false}
                          name="flooring"
                        />
                      </Col>
                    </Row>
                  </div>
                  <div className="mb-2">
                    <h5 className="mb-1">
                        {/* <User className="mr-50" size={16} /> */}
                        <span className="align-middle">Track Deferred Down Payment</span>
                    </h5>
                    <hr></hr>
                    <Row>
                      <Col sm="3" xs="6" className="text-right">
                        <div className="p" style={{paddingTop: "4px"}}>
                            <span className="mr-1" >Track Pickups</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <Radio
                          color="success"
                          defaultChecked={false}
                          name="pickups"
                        />
                      </Col>
                      <Col sm="3" xs="6" className="text-right">
                        <div className="p" style={{paddingTop: "4px"}}>
                            <span htmlFor="" className="mr-1" >Hide Pickups</span>
                        </div>
                      </Col>
                      <Col sm="3" xs="6">
                        <Radio
                          color="info"
                          defaultChecked={false}
                          name="pickups"
                        />
                      </Col>
                      <Col sm="12" xs="12" className="text-right pt-3">
                        <Button.Ripple className="mr-1" color="primary"
                            onClick={()=>this.saveChanges()}
                        >
                            Save Changes
                        </Button.Ripple>
                      </Col>
                    </Row>
                  </div>
                </CardBody>
            </Card>
          </div>
        </React.Fragment>
      )
  }
}
const mapStateToProps = state => {
  return {
    app: state.calender
  }
}

export default connect(mapStateToProps, {})(CompanySetting)
